.order-4, .order-4:after, .caret-4-desc, .caret-4-asc{
    border-right: 0.25rem solid transparent;
    border-left: 0.25rem solid transparent;
    margin-left: 0.5rem;
    display: inline-block;
    vertical-align: middle;
}
.order-4 {
    border-bottom: 0.25rem solid #b6c1d2;
    border-top: 0;
    transform: translateY(-70%);
    position: relative;
}

.order-4:after {
    content: '';
    position: absolute;
    left: -0.75rem;
    top: 0.35rem;
    border-top: 0.25rem solid #b6c1d2;
    border-bottom: 0;
}
.caret-4-desc {
    border-bottom: 0.25rem solid #b6c1d2;
    border-top: 0;
}

.caret-4-asc{
    border-top: 0.25rem solid #b6c1d2;
    border-bottom: 0;
}
[data-row-selection], .sortable { cursor: pointer; }
[data-row-selection]:focus, .sortable:focus { outline: none; }
